.main-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 60px;
  padding-bottom: 60px;
  .logo {
    a {
      display: block; } }
  .main-menu {
    ul {
      margin-bottom: 0;
      margin-top: 0;
      padding-left: 0;
      li {
        display: block;
        margin-left: 44px;
        &:first-child {
          margin-left: 0; }
        a {
          font-size: 16px;
          color: $text-grey;
          font-family: $semibold-font;
          text-decoration: none; } } } } }




