.bg-color {
  animation: 1s bgColor forwards linear; }
.opacity-0 {
  animation: 1s opacityFrom forwards linear; }
.opacity-1 {
  animation: 1s opacityTo forwards linear; }
.color {
  animation: 1s colorMy forwards linear; }
.width {
  animation: 1s widthFrom forwards linear; }
.delay-1s {
  animation-delay: 1s; }
.delay-2s {
  animation-delay: 2s; }
.delay-3s {
  animation-delay: 3s; }
.delay-4s {
  animation-delay: 4s; }
.delay-5s {
  animation-delay: 5s; }

@keyframes bgColor {
  0% {
    background-color: #F5F5F5; }
  100% {
    background-color: rgba(237, 237, 237, 0.57); } }
@keyframes opacityFrom {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }
@keyframes opacityTo {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
@keyframes colorMy {
  0% {
    color: #73738D; }
  100% {
    color: #222; } }
@keyframes widthFrom {
  0% {
    width: 0; }
  100% {
    width: 100%; } }
